import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"

export default function Colmun({ pageContext }) {
  const { title, updatedAt, image } = pageContext.column;
  const body = pageContext.column.body.childMarkdownRemark.html;

  return (
      <Layout>
        <Seo title={title} />
        <div>
            <h1>{title}</h1>
            <p>{updatedAt}</p>
        </div>
        <img src={image.file.url} alt="post-cover"></img>
        <div dangerouslySetInnerHTML={{ __html: body }} />
      </Layout>
  )
}